@use 'base/variables' as v;
@use 'base/mixins' as m;

.galeria {
    padding: 5rem 2rem;
    h3 {
        text-align: center;
    }
}

.galeria-imagenes {
    @include m.resetear-lista;
    @include m.grid(1,2rem);
    margin-top: 5rem;
    @include m.telefono {
        @include m.grid(3,2rem);
    }
    @include m.tablet {
        @include m.grid(4,2rem);
    }
    @include m.desktop {
        @include m.grid(6,2rem);
    }
    img {
        height: 60vw;
        object-fit: cover;
        @include m.telefono {
            height: 15rem;
        }
    }
}

.overlay {
    z-index: 1;
    position: fixed;
    top: 0;
    left: 0;
    bottom: 0;
    right: 0;

    width: 100%;
    height: 100%;

    display: grid;
    place-items: center;

    background-color: rgb(0 0 0 / 0.5);

    img {
        width: 80vw;
        @include m.telefono {
            width: 50vw;
        }
        @include m.tablet {
            width: 40vw;
        }
        @include m.desktop {
            width: 30vw;
        }
        @include m.desktopXL {
            width: 20vw;
        }
    }
}

.btn-cerrar {
    width: 3rem;
    height: 3rem;
    border-radius: 50%;
    background-color: v.$blanco;
    color: v.$oscuro;
    display: grid;
    place-items: center;
    font-weight: bold;
    position: absolute;
    margin-top: 80vh;
    transition: background-color 0.5s;

    &:hover {
        cursor: pointer;
        background-color: darken(v.$blanco,30%);
    }
    @include m.telefono {
        width: 3rem;
        height: 3rem;
        font-size: 1rem;
        margin-top: 82vh;
    }
    @include m.tablet {
        width: 4rem;
        height: 4rem;
        font-size: 1.5rem;
        margin-top: 70vh;
    }
    @include m.desktop {
        margin-top: 80vh;
    }
}