@use "base/variables" as v;
@use "base/mixins" as m;

.main-papers {
  margin-right: 0;
  margin-left: 0;
  margin-top: 11rem;
  @include m.tablet {
    margin-top: 24rem;
  }
  @include m.desktop {
    margin-top: 35rem;
  }
  p{
      margin-top: 1rem;
      margin-bottom: 0;
  }
  a {
    font-size: 1.2rem;
    @include m.telefono {
      font-size: 1.35rem;
    }
    @include m.tablet {
      font-size: 1.5rem;
    }
    @include m.desktop {
      font-size: 1.75rem;
    }
  }
  .patents-text {
      margin-top: 2rem;
  }
  .list-group-item {
      padding: 1rem 1rem;
  }
}

.margin-center {
  margin-right: 2rem;
  margin-left: 2rem;
  @include m.telefono {
    margin-right: 5rem;
    margin-left: 5rem;
  }
  @include m.desktopXL {
    margin-right: 15rem;
    margin-left: 15rem;
  }
}

/* The hero image */
.mini-hero-image {
  /* Set a specific height */
  height: 3rem;
  @include m.tablet {
    height: 5.5rem;
  }
  @include m.desktop {
    height: 8rem;
  }

  // background-color: red;
  background: linear-gradient(to bottom right, #023b58, white);

  width: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
}

/* Place text in the middle of the image */
.mini-hero-text {
  font-weight: bold;
  color: v.$blanco;
  font-size: 1.225rem;
  width: 100%;
  margin-top: auto;
  margin-bottom: auto;
  text-align: center;
  @include m.telefono {
    font-size: 1.8rem;
  }
  @include m.tablet {
    font-size: 2.2rem;
  }
  @include m.desktop {
    font-size: 3rem;
  }
}

.granted {
  color: v.$secundario;
  font-weight: bold;
}

.pending {
  color: green;
  font-weight: bold;
}

.patent-link {
    text-align: start;
}