@use "base/variables" as v;
@use "base/mixins" as m;

.resources {
  display: flex;
  flex-direction: column;
  .imagen-resources {
    width: 15rem;
    padding-top: 1.5rem;
    padding-bottom: 0rem;
    margin-left: auto;
    margin-right: auto;
  }
  .text-resources {
    order: -1;
    ul {
      padding-left: 0;
      margin-top: 2rem;
    }
    li {
      margin: 1rem 0;
      text-align: center;
      list-style-position: inside;
      list-style-type: none;
    }
  }
  padding: 2rem;
  @include m.telefono {
    display: flex;
    flex-direction: row;
    .imagen-resources {
      flex: 0 0 50%;
      margin-left: 0;
      display: flex;
      flex-direction: column;
      justify-content: center;
      padding-top: 0;
    }
    .text-resources {
      order: 2;
      h3,
      li {
        font-size: 1.3rem;
      }
      h3 {
        font-size: 2rem;
        text-align: center;
      }
      ul {
        margin-top: 0.5rem;
        margin-bottom: 0;
      }
      flex: 0 0 50%;
      padding-left: 2rem;
      display: flex;
      flex-direction: column;
      justify-content: center;
    }
  }
  @include m.desktop {
    .imagen-resources {
      flex: 0 0 50%;
      margin-left: 0;
    }
    .text-resources {
      h3 {
        font-size: 2.5rem;
      }
      li {
        font-size: 1.5rem;
        margin: 1rem 0;
        list-style-position: inside;
        list-style-type: disc;
      }
      h3,
      li {
        text-align: start;
      }
      padding: 0 4rem;
      flex: 0 0 50%;
    }
  }
  @include m.desktopXL {
    width: 70%;
    margin-left: auto;
    margin-right: auto;
  }
}

.resources-container {
  margin-bottom: 2rem;
  @include m.tablet {
    margin-bottom: 5rem;
  }
}

.accordion-labs {
  .accordion-text {
    margin: auto 1rem;
    text-align: center;
    color: v.$secundario;
    @include m.telefono {
      display: flex;
      flex-direction: column;
      justify-content: center;
      align-items: center;
    }
    p {
        text-align: center;
        color: v.$oscuro;
        padding-top: 1rem;
        padding-bottom: 0.5rem;
    }
  }
  .accordion-lab {
    margin-top: 2rem;
  }
  .accordion-body {
    padding-top: 0;
    padding-bottom: 0;
    @include m.telefono {
      display: grid;
      grid-template-columns: 1fr 1fr;
    }
  }
  .accordion-image {
      @include m.telefono {
          order: -1;
      }
  }
}
