@use "base/variables" as v;
@use "base/mixins" as m;

.carousel {
  .carousel-inner {
    width: 60%;
    margin-left: auto;
    margin-right: auto;
    @include m.tablet {
      width: 50%;
    }
    @include m.desktop {
      width: 40%;
    }
    @include m.desktopXL {
      width: 30%;
    }
  }
  .carousel-control-prev {
    left: 5%;
    width: 5%;
    @include m.tablet {
      left: 12%;
      width: 15%;
    }
    @include m.desktop {
      left: 17%;
    }
    @include m.desktopXL {
      left: 22%;
    }
  }
  .carousel-control-next {
    right: 5%;
    width: 5%;
    @include m.tablet {
      right: 12%;
      width: 15%;
    }
    @include m.desktop {
      right: 17%;
    }
    @include m.desktopXL {
      right: 22%;
    }
  }
}

.carousel-control-prev-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23226e93' viewBox='0 0 8 8'%3E%3Cpath d='M5.25 0l-4 4 4 4 1.5-1.5-2.5-2.5 2.5-2.5-1.5-1.5z'/%3E%3C/svg%3E") !important;
  @include m.desktop {
    width: 3rem;
    height: 3rem;
  }
  @include m.desktopXL {
    width: 4rem;
    height: 4rem;
  }
}

.carousel-control-next-icon {
  background-image: url("data:image/svg+xml;charset=utf8,%3Csvg xmlns='http://www.w3.org/2000/svg' fill='%23226e93' viewBox='0 0 8 8'%3E%3Cpath d='M2.75 0l-1.5 1.5 2.5 2.5-2.5 2.5 1.5 1.5 4-4-4-4z'/%3E%3C/svg%3E") !important;
  @include m.desktop {
    width: 3rem;
    height: 3rem;
  }
  @include m.desktopXL {
    width: 4rem;
    height: 4rem;
  }
}

