@use "base/variables" as v;
@use "base/mixins" as m;

.fa-solid {
  text-align: center;
  font-size: 2rem;
  @include m.tablet {
    font-size: 3rem;
  }
  @include m.desktopXL {
    font-size: 4rem;
  }
}

.links {
  text-align: center;
  margin-top: 4rem;
  .link {
    padding-top: 0vh;
    padding-bottom: 0vh;
    margin: auto;
    margin-bottom: 2rem;
    border: 1px solid lighten(v.$oscuro, 70%);
    border-radius: 1rem;
    // width: 12rem;
    width: 80%;
    transition: background-color 0.25s;
    &:hover {
      background-color: lighten(v.$oscuro, 70%);
    }
    @include m.telefono {
      // width: 14rem;
      // height: 15vh;
      // display: flex;
      // align-items: center;
      // justify-content: center;
    }
    @include m.tablet {
      height: 12vh;
      width: 20rem;
    }
    @include m.desktop {
      height: 11rem;
      width: 20rem;
    }
    @include m.desktopXL {
      width: 25rem;
    }
  }
  .text-center {
    margin-bottom: 2rem;
    margin-left: 1rem;
    margin-right: 1rem;
    color: v.$secundario;
  }
  .image {
    color: v.$oscuro;
    padding-bottom: 1rem;
    margin-top: 2rem;
  }
  link {
    &:hover {
      background-color: lighten(v.$oscuro, 60%);
    }
  }
  a {
    display: flex;
    flex-direction: column;
    justify-content: center;
  }
}

.main-links {
  margin-top: 5vh;
}

.links-research {
  text-align: center;
  @include m.telefono {
    display: grid;
    grid-template-columns: 1fr 1fr;
  }
}

.links {
  address {
    margin: 0;
  }
  h3 {
    text-align: center;
    padding-bottom: 2rem;
  }
  a {
    font-size: 1.3rem;
    @include m.telefono {
      font-size: 1.4rem;
    }
    @include m.tablet {
      font-size: 1.6rem;
    }
    @include m.desktop {
      font-size: 1.7rem;
    }
  }
}

@include m.tablet {
  .links {
    display: grid;
    grid-template-columns: 1fr 1fr 1fr;
    .link {
      display: grid;
      grid-template-rows: 2fr 1fr;
    }
  }
}

.link {
  padding-bottom: 2.5rem;
}
